<template>
  <form action="/">
    <van-search
      v-model="keyword"
      placeholder="请输入搜索关键词"
      show-action
      @search="onSearch"
      @cancel="onCancel"
    />

     <List2 :goods="goods">
     </List2>



<div class="Word"  v-if="flag">
  热搜：
  <van-tag type="danger" plain size="large" class="tag">衬衫</van-tag>
<van-tag type="danger" plain size="large" class="tag">T恤</van-tag>
<van-tag type="danger" plain size="large"  class="tag">西装</van-tag>
<van-tag type="danger" plain size="large" class="tag">西裤</van-tag>
<van-tag type="danger" plain size="large" class="tag">礼服</van-tag>
</div>


  </form>
</template>

<script>
export default {
    data(){
        return {
        keyword:"",
        goods:[],
        flag:true

        }
    },
    methods:{
        onSearch(){
           this.flag=false;
          //alert(this.keyword)
             this.$axios.get("/vue/getGoods",{
                params:{
                    keyword:this.keyword
                }
            }).then(res=>{
                this.goods = res.data.result;
            })


        },
         onCancel(){
            this.$router.go(-1);
        },

    }

}

</script>

<style lang="scss" scoped>
.Word{
 
  margin-top: 50px;
  
}

.tag{
  margin-left: 10px;

}

</style>

